body {
  margin: 0;
  position: relative;
  font-family: 'Raleway', sans-serif;
  background-color: #f9f6ea;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}
.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

button {
  font-size: 23px;
  font-weight: 500;
  padding: 10px 20px;
  margin-left: 20px;
  margin-top: 10px;
  border-radius: 20px;
  background-color: rgb(179, 245, 195);
  box-shadow: 0px 0px 4px 3px rgba(49, 64, 53, 0.5);
  -webkit-box-shadow: 0px 0px 4px 3px rgba(89, 64, 53, 0.5);
  -moz-box-shadow: 0px 0px 4px 3px rgba(9, 64, 53, 0.5);
  transform: scale(1);
  transition: background-color 1000ms ease, box-shadow 500ms ease,
    transform 500ms ease;
}
button:hover,
button:focus {
  background-color: #a1f5f0;
  box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  -webkit-box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  -moz-box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  transform: scale(1.1);
}

input {
  /* display: block; */
  font: inherit;
  font-size: 24px;
  padding: 10px;
  max-width: 400px;
  /* margin: 50px auto 20px 50px; */
  border-radius: 20px;
  background-color: rgb(249, 247, 240);
  box-shadow: 0px 0px 3px 2px rgba(75, 67, 67, 0.5) inset;
  -webkit-box-shadow: 0px 0px 3px 2px rgba(83, 71, 71, 0.5) inset;
  -moz-box-shadow: 0px 0px 3px 2px rgba(59, 53, 53, 0.5) inset;
}
.search-form {
  width: 100%;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: inherit;
  padding: 20px 20px;
  text-align: center;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 20px;
  gap: 30px;
  margin: 0 auto 20px auto;
  width: 100%;
}

.gallery-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  height: 100%;
  /* border-radius: 20px; */
  background-color: rgb(235, 249, 238);
  box-shadow: 0px 0px 4px 3px rgba(57, 70, 60, 0.5);
  -webkit-box-shadow: 0px 0px 4px 3px rgba(57, 70, 60, 0.5);
  -moz-box-shadow: 0px 0px 4px 3px rgba(57, 70, 60, 0.5);
  transform: scale(1);
  transition: background-color 1000ms ease, box-shadow 500ms ease,
    transform 500ms ease;
}
.gallery-item:hover {
  background-color: rgb(222, 244, 197);
  box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  -webkit-box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  -moz-box-shadow: 0px 0px 6px 5px rgba(28, 32, 29, 0.5);
  transform: scale(1.1);
}
.gallery-item__link {
  display: block;
  /* width: calc((100% - 120px) / 4); */
  flex-basis: calc((100% - 60px) / 3);
  color: inherit;
  height: auto;

  text-decoration: none;
}
.gallery-item__img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}
.gallery-item__info {
  display: block;
  padding: 10px;
}
.info-item {
  display: inline-block;
  padding: 0 5px;
}
.load-more {
  display: block;
  margin: 20px auto;
  text-align: center;
}

.is-hidden {
  display: none;
}
@media screen and (max-width: 600px) {
  button {
    font-size: 20px;
    padding: 5px 10px;
  }
  input {
    font-size: 20px;
    max-width: 250px;
  }
  .search-form {
    padding: 15px 10px;
  }

  .gallery-item__info {
    display: none;
  }
  .gallery {
    padding: 0 10px;
    gap: 10px;
    margin: 0 auto 10px auto;
  }
}
